:root {
  --bouncy-easing: cubic-bezier(0.68, -0.55, 0.265, 1.55);
}

html,
body {
    height: 100%;
    width: 100%;
    overflow: auto;
}

.App {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: justify;
        justify-content: space-between;
    min-height: 100vh;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    padding-bottom: 80px;
    overflow: hidden;
}
.table {
    width: 100%;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    text-align: center;
    position: relative;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    padding-right: 9%;
}

.table__col {
    width: 9%;
    height: 4.5vh;
    position: relative;
    padding: 0;
    margin: 0;
    border: 0;
    
    border-radius: 50%;
    background-color: #e2e2e2;
}

.table__head {
    color: #666;
    font-size: .75rem;
    background-color: #fff;
}

.number {
    display: -ms-flexbox;
    display: flex;
    width: 100%;
    text-align: center;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    height: 2rem;
}

.number--byOne {
    background-color: aquamarine;
}

.number--sameNumbers {
    background-color: lightblue;
}

.display {
    text-align: center;
}

.selected {
    text-align: center;
}
.number--selected {
    background-color: darkorange;
}
.correct {
    /* @keyframes duration | timing-function | delay |
iteration-count | direction | fill-mode | play-state | name */
    -webkit-animation: correctAnimation .325s linear;
            animation: correctAnimation .325s linear;
}
.fail {
    -webkit-animation: failAnimation .3s var(--bouncy-easing);
            animation: failAnimation .3s var(--bouncy-easing);
}

.solution {
    min-width: 3rem;
    height: 1.2rem;
    display: inline-block;
    line-height: 1rem;
    text-align: center;
    border-radius: .5rem;
    background-color: #e2e2e2;
    margin: 0 .25rem;
}

@-webkit-keyframes correctAnimation {
    0%, 100% {
        color: currentColor;
        -webkit-transform: scale(1);
                transform: scale(1);
    }
    25% {
        -webkit-transform: scale(.8);
                transform: scale(.8);
    }
    50% {
        color: #bada55;
    }
    75% {
        -webkit-transform: scale(1.5);
                transform: scale(1.5);
    }
}

@keyframes correctAnimation {
    0%, 100% {
        color: currentColor;
        -webkit-transform: scale(1);
                transform: scale(1);
    }
    25% {
        -webkit-transform: scale(.8);
                transform: scale(.8);
    }
    50% {
        color: #bada55;
    }
    75% {
        -webkit-transform: scale(1.5);
                transform: scale(1.5);
    }
}
@-webkit-keyframes failAnimation {
    0%, 100% {
        color: currentColor;
        -webkit-transform: translateX(0);
                transform: translateX(0);
    }
    25%, 75% {
        -webkit-transform: translateX(-5%);
                transform: translateX(-5%);
    }
    50% {
        color: hotpink;
        -webkit-transform: translateX(0);
                transform: translateX(0);
    }
    75% {
    -webkit-transform: translateX(5%);
            transform: translateX(5%);
    }
}
@keyframes failAnimation {
    0%, 100% {
        color: currentColor;
        -webkit-transform: translateX(0);
                transform: translateX(0);
    }
    25%, 75% {
        -webkit-transform: translateX(-5%);
                transform: translateX(-5%);
    }
    50% {
        color: hotpink;
        -webkit-transform: translateX(0);
                transform: translateX(0);
    }
    75% {
    -webkit-transform: translateX(5%);
            transform: translateX(5%);
    }
}


